.App {
	text-align: center;
}

.App-logo {
	height: 40vmin;
	pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
	.App-logo {
		animation: App-logo-spin infinite 20s linear;
	}
}

.App-header {
	background-color: #282c34;
	height: 100px;
	padding: 20px;
	color: white;
}

.App-link {
	color: #61dafb;
}

@keyframes App-logo-spin {
	from {
		transform: rotate(0deg);
	}
	to {
		transform: rotate(360deg);
	}
}

.navigation {
	background: #282c34;
}

.navigation ul {
	list-style-type: none;
	margin: 0;
	padding: 0;
}

.navigation a:link,
.navigation a:visited {
	color: white;
	text-decoration: none;
}

.active {
	font-weight: bold;
	color: red !important;
}

.error {
	font-weight: bold;
	color: red !important;
}

.forgotPassword {
	background: none !important;
	border: none !important;
	text-decoration: underline;
	margin-left: 5px;
	color: #069;
}

.forgotPassword:hover {
	text-decoration: none;
	color: red;
}

.checked {
	color: orange
}

.searchForm {
	display: inline;
}

.weblink {
	font-size: 30px;
	padding-bottom: 20px;
}

.searchBar {
	padding-bottom: 15px;
}

.navigation {
	display: flex;
	justify-content: center;
  	align-items: center;
  	height: 100px;
 	width: 100%;
}

.landing{
	margin-top: 150px;
	text-align:center;
}
.navigation > * {
	padding-left: 15px;
	padding-right: 15px;
}

.searchForm > * {
	font-size: 30px;
}

.allButton {
	font-size: 30px;
}

.starsBig > * {
	font-size: 28px;
}

.starsBig {
	padding-top: 4px;
}